html, body {
  font-family: 'Roboto', sans-serif;
  margin: auto;
  max-width: 1200px;
  background-color: rgb(255, 254, 221);
}

button:hover{
  cursor: pointer;
}

button:disabled{
  cursor: not-allowed;
}


.homePage{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.homepageContent{
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 80vh;
  overflow: auto;
}


/* NAVIGATION =========================================================== */
.navigation{
  height: fit-content;
  background-color: #272727;
  color: white;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 20px;
  padding-bottom: 0;
  padding-top: 0;
  
}

.navigation > div{
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;

}

.navigation > div > h1{
  width: 100%;
  text-align: center;
  font-size: 3rem;
  margin-bottom: 10px;
}

.navigation div > h4{
  font-size: 1rem;
  margin-left: auto;
  margin-right: 0;
  font-weight: normal;
}

.navigation > a{
  width: 25%;
  
  
}

.navigation > a > button{
  width: 100%;
  text-align: center;
  background-color: #272727;
  color: white;
  border: none;
  font-size: 1rem;
  /* border: 1px solid white; */
  height: 100%;
}

.navigation > button{
  width: 15%;
  text-align: center;
  background-color: #272727;
  color: white;
  border: none;
  font-size: 1rem;
  
  /* border: 1px solid white; */
}

.navigation > p{
  width: 15%;
  font-size: 0.8rem;
  text-align: center;
}

@media only screen and (max-width: 1100px){
  .navigation{
    padding: 5px;
    height: 20vh;
  }
  .navigation > div{
    flex-direction: row;
  }
  .navigation > div > h4{
    font-size: 0.8rem;
    margin-top: 30px;
    margin-bottom: 0;
  }
  .navigation > div > h1{
    font-size: 2rem;
  }
}





/* DESCRIPTION ================================================== */


.description{
  width: 48%;
  padding: 1rem;
  text-align: left;
  align-items: center;
}


.description > h1{
  font-size: 4rem;
  margin-bottom: 0.5rem;
}

.description > p{
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}


.description >button{
  height: 50px;
  width: 200px;
  font-size: 1rem;
  color: white;
  background-color: black;
  border-radius: 5px;
  border: none
}

@media only screen and (max-width: 1100px){
  .description{
    width: 100%;
  }
}

/* CAROUSEL ====================================================== */

.carousel{
  width: 46%;
  padding: 1rem;
}

.carousel > img{
  max-width: 100%;
}

@media only screen and (max-width: 1100px){
  .carousel{
    width: 100%;
  }
}

/* DETAILS ================================================================= */

.details{
  width: 100%;
  margin-top: 3rem;
  display: flex;
  justify-content: space-between;
}

.details > div{
  width: 30%;
  text-align: center;
  background-color: white;
  padding: 10px;
}

.details > div > h1{
  font-size: 4rem;
  color: rgb(245, 38, 38);
  margin: 3px;
}

.details > div > h2{
  margin: 3px;
}

.details > div > h3{
  margin: 3px;
}

@media only screen and (max-width: 1100px){
  .details{
    flex-wrap: wrap;
    padding: 10px;
  }
  .details > div{
    width: 100%;
    margin-bottom: 10px;
  }
}



/* SIGNIN PAGE ====================================================================== */

.signinTitle{
  font-size: 3rem;
  text-align: center;
}

.signinForm{
  display: flex;
  flex-direction: column;
  font-size: 2rem;
  
}

.signinForm > div{
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  margin-bottom: 10px;
  width: 100%;
  align-items: center;
}

.signinForm > div > label{
  width: 50%;
  text-align: left;
  font-size: 1.5rem;
}

.signinForm > div > input{
  width: 50%;
  font-size: 1.5rem;
  font-family: "Roboto", monospace;
}

.signinForm > div > button{
  width: 20%;
  height: 50px;
  font-size: 1rem;
  border: none;
  background-color: black;
  color: white;
}

.alreadyAcc{
  text-align: center;
}


.alreadyAcc > a > button{
  width: 10%;
  height: 30px;
  font-size: 1rem;
  border: none;
  background-color: black;
  color: white;
  margin-left: 10px;
}

@media only screen and (max-width: 1100px){
  .alreadyAcc > a > button{
    width: 20%;
  }
}




/* LOGIN PAGE ==================================================================================================== */

.logInPage{
  text-align: center;
}

.logInPage > button{
  width: 20%;
  height: 50px;
  font-size: 1rem;
  border: none;
  background-color: black;
  color: white;
  margin-left: 10px;
}

@media only screen and (max-width: 1100px){
  .logInPage > button{
    width: 30%;
  }
}

/* MENU CONTAINER ================================================ */

.menuandCart{
  display: flex;
  justify-content: space-between;
  max-width: 100%;
}

.menuContainer{
  height: 80vh;
  width: 70%;
  margin-top: 20px;
  overflow: auto;
  background-color: #272727;
  padding: 10px;
  /* border-radius: 10px; */
}

@media only screen and (max-width: 1100px){
  .menuContainer{
    height: 75vh;
    width: 60%;
  }
}


/* MENU ITEMS  ============================================================*/

.itemHeader{
  width: 100%;
 
}

.itemHeader > h3{
  font-size: 1.5rem;
  background-color: #272727;
  color: white;
  font-weight: normal;
  width: 100%;
  text-align: center;
  margin-bottom: 0;
}

.itemContentCards{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 10px;
}

.itemContentCards > div{
  background-color: white;
  padding: 10px;
  /* border-radius: 10px; */
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.itemContentCards > div > h3{
  width: 60%;
  font-size: 1.3rem;
  margin: 0;
}

.itemContentCards > div > h4{
  width: 20%;
  text-align: center;
  margin: 0;
}

.itemContentCards > div > button{
  padding: 0;
  margin: 0;
  width: 20%;
  height: 20px;
  border: 1px solid black;
  text-align: center;
  background-color: rgb(248, 78, 78);
  font-size: 0.7rem;
}

@media only screen and (max-width: 1100px){
  .itemContentCards{
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 10px;
  }

  .itemContentCards > div > h3{
    font-size: 1.1rem;
  }
  

}

/* CART CONTAINER ====================================================== */

.cartContainer{
  width: 25%;
  text-align: center;
  background-color: #272727;
  padding: 10px;
  margin-top: 20px;
  /* border-radius: 10px; */
}

.cartItemContainer{
  width: 100%;
  height: 70%;
  overflow: auto;
  display: flex;
  flex-direction: column;
  text-align: center;
  
}

.cartItemContainer > div{
  background-color: white;
  width: 94%;
  display: flex;
  justify-content: space-between;
  text-align: center;
  align-items: center;
  padding: 5px;
  /* border-radius: 5px; */
  height: 2.5rem;
  margin-bottom: 10px;
}

.cartItemContainer > div > h4{
  font-size: 1rem;
  width: 50%;
  
}

.cartItemContainer > div > p{
  font-size: 0.8rem; 
}

.cartItemContainer > div > button{
  font-size: 0.8rem;
  background-color: rgb(248, 78, 78); 
  border: 1px solid black
}

.cartDetails{
  background-color: white;
  height: 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  /* border-radius: 10px; */
  margin-top: 10px;
}

.cartDetails > p{
  font-weight: bold;
  font-size: 1.5rem;
}

.cartDetails > a{
  width: 60%;
  height: 40px;
}

.cartDetails > a >button{
  width: 100%;
  height: 40px;
  background-color: rgb(252, 252, 62);
  font-size: 1rem;
  
}

@media only screen and (max-width: 1100px){
  .cartContainer{
    width: 30%;
  }

  .cartItemContainer{
    height: 50vh;
  }
  .cartItemContainer > div{
    width: 100%;
    height: 4rem;
    padding: 0;
  }

  .cartItemContainer > div > h4{
    font-size: 0.7rem;
  }
  .cartItemContainer > div > p{
    font-size: 0.7rem;
    margin-left: 4px;
    margin-right: 4px;
  }
  .cartItemContainer > div > button{
    font-size: 0.7rem;
  }
}




/* CHECK OUT PAGE ================================================================================================================== */
.chkOutFormContainer{
  width: 100%;
  text-align: center;
  margin-top: 20px;
}


.checkoutCart{
  width: 100%;
  text-align: center;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.checkoutCart > button{
  width: 15%;
  height: 40px;
  background-color: #272727;
  color: white;
  border: none;
}

.checkoutCart > div{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 50%;
  height: 100%;
  margin-top: 20px;
  align-items: center;
  background-color: white;
}


/* VIEW CURRENT ORDER PAGE ==========================================================================================*/

.currentorderContainer{
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin-top: 20px;
  justify-content: center;
}

.currentorderContainer > div{
  display: flex;
  flex-wrap: wrap;
  width: 70%;
  background-color: white;
  /* line-height: 0.5rem; */
  padding: 15px;
  margin-top: 30px;
  border-radius: 10px;

}

.currentorderContainer > div > h2{
  width: 60%;
  /* line-height: 0.4rem; */
}

.currentorderContainer > div > h4{
  width: 40%;
  /* line-height: 0.4rem; */
}

.currentorderContainer > div > h3{
  width: 100%;
  /* line-height: 0.4rem; */
}

.currentorderContainer > div > p{
  text-align: center;
  margin-left: 5px;
  margin-right: 5px;
  margin-top: 5px;
  margin-bottom: 0;
  background-color: rgb(207, 207, 207);
  padding: 5px;
  font-size: 1rem;
  border-radius: 5px;
  
}

.currentorderContainer > div > h5{
  width: 100%;
  font-size: 1.5rem;
  line-height: 0.4rem;
}

.currentorderContainer > div > div{
  width: 100%;
  display: flex;
  justify-content: space-around;
}

.currentorderContainer > div > div > button{
  border: none;
  width: 30%;
  height: 50px;
  font-size: 1.5rem;
}

.currentorderContainer > div > div > button:hover{
  cursor: default;
}

@media only screen and (max-width: 1100px){
  .currentorderContainer > div{
    width: 85%;
    padding: 5px;
  }
  .currentorderContainer > div > h4{
    line-height: 1rem;
    text-align: center;
  }

  .currentorderContainer > div > div > button{
    width: 45%;
  }
}


/* ADMIN PAGE ================================================================================================================= */


.adminPageContainer{
  display: flex;
  
  width: 100%;
}

.sideBar{
  margin-top: 10px;
  width: 15%;
  display: flex;
  flex-direction: column;
  padding: 15px;
  border-right: 2px solid #272727; 
}

.sideBar > button{
  margin-top: 50px;
  height: 60px;
  font-size: 1rem;
  background-color: white;
}

.adminDetails{
  width: 80%;
  height: 70vh;
  overflow: auto;
}

.menuEditContainer{
  display: flex;
  flex-direction: column;
  width: 90%;
  padding: 20px;
}

.navBtn{
  width: 100%;
  display: flex;
  justify-content: space-around;
  margin-top: 30px;
}

.navBtn > button{
  width: 30%;
  height: 40px;
  background-color: white;
}

.addItemsContainer{
  margin-top: 30px;
  text-align: center;
}


.editItemsContainer{
  margin-top: 60px;
  width: 100%;
  display: flex;
  flex-direction: column;
  text-align: center;
}

.editItemsContainer > select{
  height: 40px;
  width: 40%;
  font-size: 1.3rem;
}

@media only screen and (max-width: 1100px){
  .adminDetails{
    height: 80vh;
  }
  .addItemsContainer>form{
    width: 100%;
  }
  .addItemsContainer>form>div>button{
    width: 80%;
  }


}